.header {
    display: flex;
    justify-content: space-around;
    position: fixed;
    top: 0;
    left: 0; 
    right: 0; 
    z-index: 9999999;
    box-shadow: 0px 8px 10px rgba(0, 0, 0, 0.2);  
    background-color: #ffffff;
  
    .logo {
      height: 100%;
      line-height: var(--height-header);
    }
  
    .nav {
      height: var(--height-header);
  
      ul {
        height: 100%;
        display: flex;
        align-items: center;
  
        li {
          list-style: none;
          text-align: center;
  
          a {
            padding: 16px;
            font-weight: 600;
            position: relative;
            margin-right: 20px;
          }
        }
      }
    }
  }
  